body, html {
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.App {
  width: 100vw;
  height: 100vh;
}

.App svg {
  position: fixed;
  top: 5vh;
  left: 5vw;
  width: 90vw;
  height: 90vh;
}

body > span {
  position: absolute;
  top: 2em;
  left: 2em;
  z-index: 100;
}


